import React, { ReactElement } from "react";
import { Stack, Typography } from "@mui/material";

interface Props {
  title: string;
  subtitle?: string;
  children?: ReactElement;
}

export const Heading: React.FC<Props> = ({ title, subtitle, children }) => {
  return (
    <Stack alignItems="center" spacing="1rem" py={{ xs: "4rem", md: "6rem" }}>
      <Typography
        variant="h2"
        fontSize={{ xs: "2rem", sm: "2.25rem", md: "2.5rem" }}
        textAlign={{ xs: "left", md: "center" }}
        fontWeight="500"
      >
        {title}
      </Typography>
      {subtitle && (
        <Typography
          fontSize={{ xs: "1rem", sm: "1.25rem", md: "1.125rem" }}
          textAlign={{ xs: "left", md: "center" }}
          color={(theme) => theme.palette.text.secondary}
          maxWidth="48rem"
        >
          {subtitle}
        </Typography>
      )}
      {children}
    </Stack>
  );
};
